.my-custom-antd-mask #myCustomAntdMask {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 2001;
}
.my-custom-antd-mask #myCustomAntdMask:before,
.my-custom-antd-mask #myCustomAntdMask:after {
  content: '';
  border-radius: 50%;
  background-color: #2db7f5;
  -webkit-animation: antSpinBounce 2.2s infinite ease-in-out;
          animation: antSpinBounce 2.2s infinite ease-in-out;
  display: block;
  position: absolute;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
